import React from 'react';
import {Link} from 'react-router-dom';

const sideDrawerItems = (props) => (
    <React.Fragment>
        <div>1. poscodie</div>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('Byt (29)', 16)}>Byt (29)</li></Link>
        <div>2. poschodie</div>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('Inf kabinet (53)', 53)}>Inf kabinet (53)</li></Link>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('III.A (61)', 61)}>III.A (61)</li></Link>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('Kniznica (70)', 70)}>Kniznica (70)</li></Link>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('Aj 1 (71)', 71)}>Aj 1 (71)</li></Link>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('Nj 2 (77)', 77)}>Nj 2 (77)</li></Link>
        <div>3. poschodie</div>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('Bio kabinet (83)', 83)}>Bio kabinet (83)</li></Link>
        <Link style={{ textDecoration: 'none' }} to="/"><li onClick={() => props.click('VI.OA (84)', 84)}>VI.OA (84)</li></Link>

        <Link style={{ textDecoration: 'none' }} to="/about-project"><div style={{ textDecoration: 'underline' }} onClick={props.aboutProjectClick}>O projekte</div></Link>
    </React.Fragment>
)


export default sideDrawerItems;